<template>    
    <div id="app" class="bg_wood">
        <div class="title" v-colors>
            <div><i class="iconfont icon-magnifier"></i></div>
            <div></div>
            <label @click="toCreateRoom"></label>
        </div>
        <div class="user">
            <span class="profile" v-colors>{{userInfo.username|firstChar}}</span>
            <input v-model="inputValue" class="user_input" @keyup.enter="modifyUser" placeholder=""></span>
            <label class="modify" @click="modifyUser"></label>
        </div>
        <h4 class="room_status" v-show="allRoomInfo.length===0">,</h4>
        <h4 class="room_status" v-show="allRoomInfo.length>0"> : </h4>
        <ul>
            <li v-for="(roomInfo,index) in allRoomInfo" class="room_list" @click="toEnterRoom(roomInfo)">
                <span class="profile" v-colors>{{roomInfo.owner|firstChar}}</span>
                <div class="list_content">
                    <label class="roomIn"> :{{roomInfo.owner}} </label>
                    <p>:{{roomInfo[roomInfo.roomID].length}}/{{roomInfo.maxNumbers}}
                        <span class="status">{{getStatus(roomInfo.roomStatus)}}</span>
                    </p>
                </div>            
            </li>
        </ul>
    </div>
</template>
<script>
    "use strict";
    import { socket,router} from './../../main'
    import userStorage from './../../localStorage'
    export default {
        data() {
            return {
                userInfo: userStorage.fetch(), //localstorage
                inputValue: "",//

            }
        },
        computed:{
            allRoomInfo(){
                return this.$store.state.allRoomInfo; // store allRoomInfo
            },
        },
        beforeCreate(){
            console.log(socket);
            socket.on('updateAllroomInfo',allRoomInfo=>{ // 
                allRoomInfo = JSON.parse(allRoomInfo);
                this.$store.commit('updateRoomInfo',allRoomInfo);
            })
        },
        beforeRouteEnter: (to, from, next) => {
            console.log(to.params.roomStatus);
            switch (to.params.roomStatus) {
                case "NoRoom":// room
                    alert(',,.');
                    next();
                    break;
                case "ExitRoom":
                     alert('!');
                     next();
                     break;
                case "GameOver":
                    alert('');
                    next();
                    break;
                case "NoReg":
                    alert('nonono!');
                    next();
                    break;
                default:
                    next();
                    break;
            }
            // if(to.params.roomStatus ==="NoRoom"){// room
            //     alert(',,.');
            // };
            // if(to.params.roomStatus === "ExitRoom"){
            //     alert('!');
            // };
            // next();
        },
        methods: {
            getStatus(roomStatus){
                switch (roomStatus) {
                    case 'isReady':
                        return '..'
                        break;
                    case'isGaming':
                        return ''
                        break;
                    default: 
                        return '..'
                        break;
                }
            },
            modifyUser() {
                if (this.inputValue.length > 8) {
                    alert('8');
                    this.inputValue = "";
                    return;
                } else {
                    if (this.inputValue.trim()) {
                        this.userInfo.username = this.inputValue.trim();
                    }
                }
                userStorage.save(this.userInfo);
            },
            toCreateRoom() {
                if (!this.userInfo.username) {
                    alert(",!");
                    return;
                }
                var roomInfo = { //   ID
                    owner: this.userInfo.username,// 
                    roomID:null, //  
                    ownerIP:null,//  
                    roomStatus:'isReady', //  ready gaming end undefined,
                    maxNumbers:6, //   
                    players: [],//{ player: this.userInfo.username, playerIP:'', }, //  
                };
                socket.emit('createRoom', roomInfo); //  createroom
                socket.on('message',function(room){
                    room = JSON.parse(room);
                    router.push({name:'room',params:{roomID:room.roomID}})
                })
            },
            toEnterRoom(roomInfo){
                if(roomInfo[roomInfo.roomID].length === roomInfo.maxNumbers){
                    alert(',!!');
                    return;
                }else if(roomInfo.roomStatus ==="isReady"){
                     router.push({name:'room',params:{roomID:roomInfo.roomID}})
                }else if(roomInfo.roomStatus ==="isGaming"){
                    router.push({name:'game',params:{roomID:roomInfo.roomID}})
                }
            },
        },        
    }

</script>
<style>
body{
   
        /*font-size: 20px;*/
    }
    .title{
        padding:8px 10px;
    }
    .user_input {
        display: inline-block;
        width: 120px;
    }

    .modify {
        font-size: 13px;
        color: white;
        border-radius: 2px;
        padding: 3px 5px;
        background-color: #00a3af;
    }
    .room_status{
        color:#fff;
        text-align:center;
        padding-top:10px;
    }
    .room_list{
        display: -webkit-flex;
        display:flex;
        align-content: center;
        padding:15px 10px;
        font-size: 15px;
        cursor:pointer;
    }
    .list_content{
        display: -webkit-flex;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    .status{
        float:right;
    }
</style>


