<template>
    <div id="app">
        <!--<div style="font-size:12px">{{allRoomInfo}}</div>-->
        <!--<div style="font-size:12px">{{getCurrentRoomInfo}}</div>-->
        <div class="title text_center" v-colors>
            <!--<span class=""><span v-fontcolors>{{getCurrentRoomInfo.drawer}}</span></span>-->
            <div v-show="isDrawer()">: {{topic}}</div>
            <div v-show="!isDrawer()">: {{prompt}}</div>
        </div>
        <div class="content">
            <div class="dialog" v-show="showAnswer">
                <p>:<br/>{{getAnswer}}</p>
            </div>
            <canvas class="canvas" v-show="isDrawer()" width="600" height="350" v-fit>
            </canvas>
            <div class="blue" v-show="!isDrawer()" v-fit>
                <img class="getCanvas" src="" v-fit></img>
            </div>
        </div>
        <input type="text" v-show="!isDrawer()" @keyup.13="sendAnswer" class="answer_input" v-model="input_value" placeholder="..">
        <ul class="player_ul clearfix">
            <li v-for="(n,index) in 6" class="player_list" v-show="getCurrentRoomInfo[getCurrentRoomInfo.roomID][index]">
                <div class="user_pro">
                    <span class="profile" v-colors>{{getCurrentRoomInfo[getCurrentRoomInfo.roomID][index]|filtersUserName}}</span>
                    <span class="score">{{getCurrentRoomInfo[getCurrentRoomInfo.roomID][index]|filtersScore}}</span>
                </div>
                <p v-show="!getCurrentRoomInfo[getCurrentRoomInfo.roomID][index]">{{""}}</p>
                <p>{{getCurrentRoomInfo[getCurrentRoomInfo.roomID][index]|judgeUser}}</p>
            </li>
        </ul>
        
    </div>
</template>
<script>
    "use strict";
    import { socket, router } from './../../main';
    import userStorage from './../../localStorage';
    export default {
        data() {
            return {
                mySelf: userStorage.fetch().username,
                getCurrentRoomInfo: {
                    "000": [{
                        player: '',
                    }],
                    roomID: "000",
                    owner: 'null',
                    drawer: 'null',
                    drawerIP: 'null',
                    roomStatus: 'null',
                },
                canvasSrc: '',
                input_value: '',
                topic: '',
                prompt: '',
                roomIndex: '',
                showAnswer: false,
                getAnswer: '',
            }
        },
        methods: {
            isDrawer() {
                if (this.mySelf) {
                    if (this.mySelf === this.getCurrentRoomInfo.drawer) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            sendAnswer() {
                if (this.input_value.trim()) {
                    if (this.input_value.length <= 20) {
                        socket.emit('sendAnswer', { answer: this.input_value, roomIndex: this.roomIndex });
                        this.input_value = "";
                    } else {
                        alert('20!');
                    };
                };

            },
        },
        computed: {
            allRoomInfo() { // 
                return this.$store.state.allRoomInfo;
            },
        },
        beforeCreate() {
            if (!userStorage.fetch().username) {
                alert('');
                this.$router.replace({ path: "/home" });
                return;
            };
            socket.on('updateAllroomInfo', allRoomInfo => { // 
                allRoomInfo = JSON.parse(allRoomInfo);
                if (allRoomInfo.length === 0) {
                    this.$router.replace({ name: "home", params: { roomStatus: 'NoRoom' } });
                    return
                }
                this.$store.commit('updateRoomInfo', allRoomInfo);
                // console.log('allRoomInfo:')
                // console.log(allRoomInfo);
                // console.log('route.roomID:')
                console.log(this.$route.params.roomID);
                this.getCurrentRoomInfo = allRoomInfo.find(room => { //  
                    return this.$route.params.roomID in room;
                })
                if (!this.getCurrentRoomInfo) {//  
                    console.log(',');
                    this.$router.replace({ name: "home", params: { roomStatus: 'ExitRoom' } });
                    return
                }
                // console.log('beforecreate');
            });
            socket.on('sendTopic', topicMsg => {
                console.log('sendTopic')
                console.log(topicMsg);
                this.topic = topicMsg.topic;
                this.prompt = topicMsg.prompt;
            })
        },
        directives: {
            fit: {
                bind(e, binding) {
                    e.width = document.body.clientWidth;
                    e.height = 350;
                }
            }
        },
        mounted() {
            socket.emit('updateAllroomInfo');
            console.log('roommounted');
            var drawCanvas = document.querySelector(".canvas");
            var getCanvas = document.querySelector(".getCanvas");
            var getContent = document.querySelector(".content");
            var canvas_context = drawCanvas.getContext('2d');
            drawCanvas.addEventListener('touchmove', function (e) { e.preventDefault() }, false); //canvas
            drawCanvas.onmousedown = (ev) => {// pc
                var event = window.event || ev;
                canvas_context.moveTo(event.clientX - getContent.offsetLeft, event.clientY - getContent.offsetTop + document.body.scrollTop);
                document.onmousemove = (ev) => {
                    //move
                    var event = window.event || ev;
                    // console.log("event.clientX "+ event.clientX);
                    // console.log('drawCanvas.offsetLeft '+ drawCanvas.offsetLeft);
                    // console.log('content left :' + getContent.offsetLeft)
                    canvas_context.lineTo(event.clientX - getContent.offsetLeft, event.clientY - getContent.offsetTop + document.body.scrollTop);
                    canvas_context.stroke();
                    console.log('onDraw')
                    socket.emit('onDraw', { url: drawCanvas.toDataURL('image/jpg', 1), roomIndex: this.roomIndex });//onDraw 
                }
                document.onmouseup = () => {
                    document.onmousemove = null;
                };
            };

            drawCanvas.ontouchstart = (ev) => {// 
                var event = ev.touches[0];
                canvas_context.moveTo(event.clientX - getContent.offsetLeft, event.clientY - getContent.offsetTop + document.body.scrollTop);
                document.ontouchmove = (ev) => {
                    var event = ev.touches[0];
                    canvas_context.lineTo(event.clientX - getContent.offsetLeft, event.clientY - getContent.offsetTop + document.body.scrollTop);
                    canvas_context.stroke();
                    socket.emit('onDraw', { url: drawCanvas.toDataURL('image/jpg', 1), roomIndex: this.roomIndex });//onDraw 

                };
                document.ontouchend = () => {
                    document.ontouchmove = null;
                };
            };
            socket.on('onDraw', url => {
                console.log('onDraw');
                getCanvas.src = url;
            });
            socket.on('rpsSendAnswer', msg => {
                // console.log('');
                console.log(msg);
            })
            socket.on('getAnswer', topic => {
                this.getAnswer = topic;
                this.showAnswer = true;
                var getAnswer = setTimeout(() => {
                    this.showAnswer = false;
                    clearTimeout(getAnswer);
                }, 3000);
            })
        },
        watch: {
            getCurrentRoomInfo: {
                handler: function (room, oldRoom) {
                    // console.log('getCurrentRoomInfo');
                    // console.log(':')
                    // console.log(room);
                    // console.log(':')
                    // console.log(oldRoom);
                    if (room.drawer != this.mySelf) {
                        var drawCanvas = document.querySelector(".canvas");
                        drawCanvas.height = 350;
                    } else {
                        var getCanvas = document.querySelector(".getCanvas");
                        getCanvas.src = "";
                    }
                    this.roomIndex = this.allRoomInfo.findIndex((room, index) => { // 
                        return this.$route.params.roomID in room;
                    })
                    if (room.roomStatus === "isReady") {
                        // this.$router.replace({ name: "room",params:{roomID:this.$route.params.roomID}});
                        this.$router.replace({ name: "home", params: { roomStatus: 'GameOver' } });
                        return;
                    }
                    if (!userStorage.fetch().username) {
                        alert('');
                        this.$router.replace({ path: "/home" });
                        return;
                    }
                    var userIndex = room[room.roomID].findIndex(key => {//
                        return userStorage.fetch().username === key.player;
                    })
                    if (userIndex < 0) {
                        alert(',!!!!!')
                        this.$router.replace({ path: "/home" });
                    }
                },
                deep: true,
            }
        },
        beforeRouteLeave: (to, from, next) => {
            console.log('to:')
            console.log(to)
            if (to.name === 'home') {
                console.log('exitRoom')
                socket.removeListener('rpsSendAnswer');// 
                socket.removeListener('rpsBeginGame');// rpsBgeingame
                socket.emit('exitRoom', from.params); //  
            }
            next();
        },
    }

</script>
<style>
    .canvas {
        background-color: white;
        width: 100%;
    }

    .blue {
        width: 100%;
        height: 350px;
        max-width: 600px;
        background: #fff;
    }

    .answer_input {
        /*position: fixed;*/
        /*bottom: 0px;*/
        max-width: 600px;
        border: none;
        border-bottom: salmon solid 2px;
        background-color: rgb(255, 255, 255);
        width: 100%;
        height: 40px;
        padding: 10px 15px;
        outline: none;
    }

    .player_list {
        float: left;
        width: 16.5%;
        padding-top: 10px;
        font-size: 12px;
    }

    .user_pro {
        position: relative;
    }

    .score {
        position: absolute;
        left: 50%;
        top: 50%;
        margin-top: -7px;
        margin-left: 10px;
        width: 17px;
        height: 17px;
        line-height: 17px;
        text-align: center;
        border-radius: 50%;
        display: inline-block;
        background-color: rgb(241, 94, 102);
        color: #fff;
    }

    .content {
        position: relative;
    }

    .dialog {
        text-align: center;
        width: 200px;
        height: 200px;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-top: -100px;
        margin-left: -100px;
        background-color: #f5b1aa;
        border-radius: 50%;
        color: #fff;
    }

    .dialog p {
        /*line-height: 200px;*/
        width: 100px;
        height: 100px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
    }

    .user_pro .profile {
        height: 40px;
        width: 40px;
        line-height: 40px;
        font-size: 20px;
    }
</style>


